@import url("https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700;800;900&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}



a {
  text-decoration: none;
}

input,
button {
  outline: none;
  border: 0;
  &:focus {
    outline: none;
    border: 0;
  }
}

body {
  font-size: 16px;
  overflow: hidden;
  font-family: "Inter", sans-serif;
}
